
.user-panel-container {
    flex-flow: row wrap;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(to bottom, #ffccee,#aa64d8);
}


.sidebar{
    display: flex;
    flex-direction: row;
    padding: 12px;
    gap: 10px;
    justify-content: space-evenly;
}
.sidebar button{
    filter: none;
    -webkit-filter: none;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-color: aquamarine;
    width: wrap-content;
    height: 50px;
    justify-content: left;
    align-content: left;
    justify-items: left;
    align-items: left;
    text-align: center;
    text-transform: uppercase;
    color: #468d1c;
    background-color: #000000;
}
.sidebar button img{
    width: 25px;
    height: 25px;
    justify-self: left;
    background-color: white;
    border-radius: 40%;
    -webkit-border-radius: 40%;
    -moz-border-radius: 40%;
    -ms-border-radius: 40%;
    -o-border-radius: 40%;
}
.sidebar button:hover{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transition: all 0.33s linear;
    -webkit-transition: all 0.33s linear;
    -moz-transition: all 0.33s linear;
    -ms-transition: all 0.33s linear;
    -o-transition: all 0.33s linear;
}
.sidebar button:active{
    box-shadow: 2px 2px #bb33ee;
}
.content-area{
    padding: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
}
.user-info{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}
.user-info h2{
     font-size: 3em;
     text-transform:none;
     color: black;
     animation: wave infinite 1s linear;
     -webkit-animation: wave infinite 1s linear;
     animation-delay: calc(var(--index) * 0.1s);
     white-space: pre; /* Keep spaces preserved */
     padding-bottom: 2px;
}
@keyframes wave {
    10%{
        text-shadow: #468d1c 0 0 10px;
    }
    40%{
        text-shadow: #48ebf7 0 0 10px;
    }
    70%{text-shadow: #ad3f1e 0 0 10px;}
    100%{text-shadow: #d8ce42 0 0 10px;}
}
.user-info div{
   width: wrap-content;
   height: auto;
   background-color: rgba(240, 248, 255, 0.445);
   display: flex;
   flex-direction: column;
   gap: 12px;
   justify-content: center;
   align-content: center;
   justify-items: center;
   align-items: center;
   padding: 5px;
   box-shadow: 5px 5px #48ebf7;
   text-wrap: wrap;
}
.user-info p{
   font-size: x-large;
   font-family: Georgia, 'Times New Roman', Times, serif;
   text-decoration: none;
   display: flex;
   flex-direction: column;
   padding-bottom: 0px;
   color: #000000;
}
.user-info strong{
    width: 100%;
    text-align: center;
    font-size: 20px;
    text-decoration: underline;
    color: rgb(63, 153, 101);
    text-decoration-color: #aa64d8;
    -moz-text-decoration-color: #aa64d8;
}
.edit{
   background-color: #d8ce42;
}
.toper{
    border-bottom: 2px solid silver;
    letter-spacing: 4px;
}
.edit-form{
    display: flex;
    flex-direction: column;
}
.edit-form div{
      background-color: transparent;
      box-shadow: none;
      width: 100%;
}
.edit-form div label{
    color: #000000;
}
.edit-form div input{
    width: 100%;
    color: #000000;
}
.edit-form div input:focus{
    background-color: black;
    color: white;
}
.abbruch{
    background-color: red;
    color: white;
}
.save{
    background-color: green;
    color: white;
}

  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen and (max-width: 768px) {
     /* Stack sidebar and content on mobile */
     .container {
        flex-direction: column;
        width: 100%;
        padding: 0;
    }
    .auth-container {
        width: 100%;
        height: 100%;
    }
    .user-panel-container{
        width: 100%;
        height: auto;
        padding: 0;
    }

    .sidebar {
        width: 100%;  /* Sidebar takes full width */
        padding: 0;
        margin-bottom: 20px;  /* Add space between sidebar and content */
    }
    .sidebar button {
        display: flex; /* Use flexbox for alignment */
        justify-content: center; /* Horizontally center content */
        align-items: center; /* Vertically center content */
        width: 100px;
        font-size: 12px;
        overflow: hidden;
        color: transparent;
        background-color: transparent; /* Remove background color if needed */
        border: none; /* Optional: Remove borders if not needed */
        cursor: pointer; /* Add a pointer cursor for better user experience */
        border: none;
    }
    
    .sidebar button img {
        max-width: 100%; /* Ensure the image scales properly */
        max-height: 100%; /* Ensure the image scales properly */
        margin: auto;
    }
    .user-info{
        width: 100%;
        margin-left: -10%;
    }
    .user-info h2{
        font-size: 12px;
        margin-left: 3%;
    }
    .user-info div{
        width: 100%;
        padding: 5px;
        background-color: transparent;
        box-shadow: none;
    }
    .user-info div p{
        font-size: 18px;
    }
    .user-info div input{
        width: 80%;
    }
    .user-info div input:focus{
        background-color: black;
        color: white;
    }
    .content-area {
        width: 100%;
        padding: 4px;
    }

    .menu-items {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));  /* Smaller grid for mobile */
        gap: 10px;
    }
  }
  
