.App{
  width: 100%;
  height: 100%;
  background: 
  rgba(144,30,29,255);
  display: flex;
  flex-direction: column;
  margin-left: -6px;
  scroll-behavior: smooth;
}
header{
  width: 100%;
  height: 174px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: fixed;
  background-color: whitesmoke;
  top: 0;
}
.App-header{
  width: 100%;
  height: 174px;
  flex-shrink: 0;
  background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
  border-bottom: 3px solid 
  rgba(144,30,29,255);
  z-index: 3;
}
.logo{
  width: 191px;
  height: 155px;
  flex-shrink: 0;
  margin-left: 16%;
  padding-right: 15px;
  animation: pumper 1s infinite linear;
  -webkit-animation: pumper 1s infinite linear;
  transition: all 0.5s linear;
  -webkit-transition: all 0.5s linear;
  -moz-transition: all 0.5s linear;
  -ms-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  border: none;
}
@keyframes pumper {
  50%{
    transform: scale(1.1);  
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
}
100%{
  transform: scale(1);
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
}
}
.pageTitle{
  width: 519px;
  height: 43px;
  flex-shrink: 0;
  color: #0f0e0e;
  font-family: Italianno;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  z-index: 0.4;
}
nav{
  width: 594px;
  height: 47px;
  flex-shrink: 0;
}
.navigator{
  width: 594px;
  height: 47px;
  flex-shrink: 0;
  background: #060606;
  display: contents;
  list-style: none;
}
.pages{
  width: 403px;
  height: 47px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  gap: 50px;
  margin-top: 3.4cm;
  list-style: none;
  margin-left: 15%;
  text-decoration: none;
}
.pages li{
  font-size: 40px;
  color: rgb(10, 10, 10);
  font-style: oblique;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-weight: lighter;
  text-decoration: none;
  padding-right: 13px;
}
.pager{
  width: 98px;
  height: 26px;
  flex-shrink: 0;
  font-family: "Jim Nightshade";
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  background: linear-gradient(90deg, #000000 0%, #af943c 98.51%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.spin{
  width: 174px;
  height: 174px;
  flex-shrink: 0;
  animation: spin 4s linear infinite;
  -webkit-animation: spin 4s linear infinite;
  transition: transform 2s linear;
  -webkit-transition: transform 2s linear;
  -moz-transition: transform 2s linear;
  -ms-transition: transform 2s linear;
  -o-transition: transform 2s linear;
}
@keyframes spin{
  50%{
    transform: rotate(360deg);  
    -webkit-transform: rotate(360deg ) ;
    -moz-transform: rotate(360deg ) ;
    -ms-transform: rotate(360deg ) ;
    -o-transform: rotate(360deg ) ;
}
}
.main{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
  color: aliceblue;
  margin-top: 170px;
}
.options{
  display: flex;
  flex-direction: row;
  gap: 12px;
  max-width: 400px;
  max-height: 50px;
  flex-grow: auto;
}
.user{
  padding-top: 100px;
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin-left: 30%;
  justify-items: last baseline;
  align-items: last baseline;
  justify-content: last baseline;
}
@keyframes imagebluer {
  50%{
    box-shadow: 3px 3px 3px #1c6351;
    transform: translate(1px);
    -webkit-transform: translate(1px);
    -moz-transform: translate(1px);
    -ms-transform: translate(1px);
    -o-transform: translate(1px);
}100%{
  box-shadow: 1px 1px 1px #af943c;
}
}
.userAcess{
  display: contents;
  width: 200px;
  height: 200px;
}
.basket{
  width: 50px;
  height: 50px;
  margin-top: 1.7cm;
}
.userInfos{
  width: 50px;
  height: 50px;
  margin-top: 1.5cm;
}
.status{
  font-size: 30px;
  font-weight: bold;
  font-family: monospace;
  color: red;
  animation: pumper 1s infinite linear;
}
.pages li:hover{
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  transition: transform 0.33s linear;
  -webkit-transition: transform 0.33s linear;
  -moz-transition: transform 0.33s linear;
  -ms-transition: transform 0.33s linear;
  -o-transition: transform 0.33s linear;
}
.call {
  width: 100px;
  height: 100px;
  display: flex;
  z-index: 2;
  background-color: #58b8a4;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 10px 0px rgba(144, 30, 29, 1);
  margin-top: -10%;
  margin-left: 4%;
  position: fixed; /* Keep element fixed during scroll */
  bottom: 20px;    /* Adjust the position to stay at the bottom */    /* Adjust the position to stay at the right */
  animation: wave 2s infinite ease-in-out; /* Apply the box-shadow wave animation */
}

@keyframes wave {
  0% {
    box-shadow: 0 0 10px rgba(31, 103, 185, 0.719);
  }
  50% {
    box-shadow: 0 0 20px rgb(102, 29, 144), 0 0 40px rgba(144, 30, 29, 0.5);
  }
  100% {
    box-shadow: 0 0 10px rgba(144, 30, 29, 0.5);
  }
}
.creater{
  font-size: 13px;
  color: #000000;
  z-index: 1;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: lighter;
  font-style: italic;
  margin-left: 40px;
}
.amount{
  font-size: 34px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bolder;
  font-style: oblique;
  color: rgba(144, 30, 29, 1);
  z-index: 1;
  margin-left: -40px;
  padding-right: 16px;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  transition: transform 0.33ms;
  -webkit-transition: transform 0.33ms;
  -moz-transition: transform 0.33ms;
  -ms-transition: transform 0.33ms;
  -o-transition: transform 0.33ms;
}
.burger {
  display: none;
  font-size: 30px;
  cursor: pointer;
}

/* Medium phones (iPhone 14, 15, modern Androids) */
@media only screen  and (max-width: 768px) {
  .App {
    width: 115%;
    height: 100%; /* Take the full viewport height */
    background-color: #000000;
    margin-left: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    border-left: none;
  }

  .App-header {
    display: flex;
    flex-direction: column;
    height: 130px;
    padding-bottom: 5px;
    z-index: 555555;
  }

  .logo {
    width: 150px;
    height: 100px;
    margin-left: 25%;
  }

  .navigator {
    min-width: 375px;
    max-width: 430px;
    font-size: 12px;
  }

  header {
    height: 20px;
    background-color: #000000;
    margin-top: -0.5cm;
  }
  .burger {
    display: block;
    font-size: 30px;
    cursor: pointer;
    margin-left: 20px;
    margin-top: -5px;
  }

  .pages {
  display: none;
  /*Hidethepagesinitiallyonmobile*/flex-direction: column;
  background-color: transparent;
  padding: 10px;
  gap: 5px;
  transform: translate(-100%);
  -webkit-transform: translate(-100%);
  -moz-transform: translate(-100%);
  -ms-transform: translate(-100%);
  -o-transform: translate(-100%);
}
.pages li a{
  font-size: 20px;
  text-decoration: underline;
}
.pages li a:active{color: brown;}

  .pages.active {
    display: flex;
    width: 100px;
    height: 200px;
    margin-left: -3px;
    z-index: 44444;
    margin-top: -0.2cm;
    border-top-right-radius: 30%;
    border-bottom-right-radius: 30%;
    transform: translate(2%);
    -webkit-transform: translate(2%);
    -moz-transform: translate(2%);
    -ms-transform: translate(2%);
    -o-transform: translate(2%);
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    background-color: #58b8a4;
}

  .user {
    width: 100px;
    height: 50px;
    z-index: 333333;
    margin-top: -15px;
    display: flex;
    flex-direction: row;
    gap: 4px;
    margin-left: 70%;
    top: auto;
    left: auto;
    position: absolute;
  }

  .basket {
    width: 40px;
    height: 40px;
  }

  .userInfos {
    width: 40px;
    height: 40px;
    margin-left: -2px;
  }

  .amount {
    font-size: 14px;
    margin-left: -8px;
  }

  .status {
    font-size: 14px;
  }

  .main {
    min-width: 375px;
    max-width: 430px;
    width: 100%;
    height: auto; /* Set auto for dynamic content */
    margin-top: 130px;
    display: flex;
    flex-grow: 1;
    border-left: none;
  }
  .call{
    width: 100px;
    height: 100px;
    margin-top: -1cm;
  }
}