.checkout-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(35, 74, 83, 0.767);
    display: flex;
    justify-content: center;
    align-items: center;
}

.checkout-content {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 60%;
    max-width: 60%;
    max-height: 60%;
    text-align: center;
    overflow-y: scroll;
    overflow-x: scroll;
}
.checkout-content .shut {
    background-color: red;
    font-size: 26px;
}
.checkout-content h2{
    font-size: 30px;
    color: green;
    text-align: center;
    padding-bottom: 10px;
}
.checkout-content h3{
    font-size: 25px;
    color: rgb(0, 0, 0);
    text-align: center;
    padding-bottom: 10px;
}

.indi{
    margin-bottom: 15px;
}
.kauf{
    width: 100%;
    height: auto;
    padding: 12px;
    font-size: 25px;
    font-style: normal;
    list-style: none;
    border: 3px solid green;
    text-align: left;
    display: flex;
    flex-direction: column;
}
.checkout-name, .checkout-address, .checkout-phone{
    width: 100%;
    height: auto;
    font-size: 15px;
    font-style: bold;
}
.checkout-name input, .checkout-address input, .checkout-phone input{
   width: 400px;
   height: 30px;
   color: #000000;
   padding-left: 12px;
   margin-left: 12px;
}
.items-list{
    display: flex;
    flex-direction: column;
    list-style: none;
    overflow-y: scroll;
}
.items-list li{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    list-style: none;
    font-size: 20px;
    color: #f44336;
}
.user-information{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #ccc;
    list-style: none;
    font-size: 20px;
}
.user-information label{
    font-size: 16px;
    color: #4CAF50;
}
.user-information input{
    width: 100%;
    height: 30px;
    padding-left: 12px;
    margin-left: 12px;
}
.order-type{
    font-size: 20px;
    justify-content: center;
    display: flex;
    flex-direction: row;
    justify-items: center;
}
.order-type label{
    font-size: 23px;
    color: #ca7e38;
    padding: 20px;
}
.payment-methods{
    display: flex;
    flex-direction: column;
    padding: 10px;
    list-style: none;
    font-size: 20px;
}
.payment-methods select{
    width: 20%;
    font-size: 20px;
    border-color: #ca7e38;
    box-shadow: 10px 10px #ccc;
}
.paypal-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    list-style: none;
    font-size: 20px;
    margin-left: 20%;
}
.stripe-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    list-style: none;
    font-size: 20px;
    margin-left: 0%;
}
.stripe-container button{
    background-color: #4CAF50;
    height: 50px;
    border-color: #ccee;
    margin-top: 10px;
}

strong, p{
    color: green;
    font-size: 20px;
}
.cash{
    background-color: #4CAF50;
    font-size: 26px;
}

/* Small phones (iPhone SE, older Android devices) */
  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen  and (max-width: 768px) {
    .checkout-content{
        width: 100%;
        height: 100%;
    }
    .payment-label{
        font-size: 20px;
    }
    .user-information input{
        margin-left: 0px;
    }
    .payment-methods select{
        width: 70%;
    }
    .paypal-container{
        margin-left: 0%;
    }
    .cash{ 
        background-color: #4CAF50;
        font-size: 26px;
    }
  }
  
  /* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */