body{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: aquamarine;
}
html{
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
}
#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: black;
}
 
/* Small phones (iPhone SE, older Android devices) */
@media only screen and (max-width: 320px) {
  body {
    /* Styles for very small phones */
    width: 100%;
    justify-content: left;
  }
  html{
    scroll-behavior: smooth;
    width: 100%;
  }
  #root {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(117, 34, 34);
  }
}

/* Medium phones (iPhone 14, 15, modern Androids) */
@media only screen and (max-width: 768px) {
  /* Ensure body takes up the full height */
  html, body {
    width: 100%;
    height: 100%;
    margin: 0; /* Reset default margin */
    padding: 0;
    background-color: rgb(253, 253, 253);
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    scroll-behavior: smooth;
    overflow-x: hidden; /* Prevent horizontal scrolling */
  }
  
  #root {
    width: 100%;
    height: 100%; /* Ensure it takes full height */
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    overflow: auto; /* Allow vertical scrolling if necessary */
  }
  
  .closed-container {
    display: flex; 
    flex-direction: column;
    margin: 0; /* Remove unnecessary margins */
    width: 100%;
    height: 100%; /* Ensure it takes full height */
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
  }
  
  .closed-header {
    height: auto; /* Make the header height dynamic */
    text-align: center; /* Center align text */
    margin-top: 3cm;
    z-index: 3333;
  }
  
  .closed-title {
    font-size: 18px; /* Adjust font size for smaller screens */
    margin: 0; /* Reset margins */
    color: #c52727;
  }
  
  .closed-message {
    font-size: 16px; /* Ensure message is readable */
    margin: 10px 0; /* Add some spacing */
    text-align: center; /* Center align the message */
    padding-bottom: 1cm;
  }
}
/* Wrapper for the closed page */
.closed-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f8f8f8;
}

/* Styling for the header */
.closed-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


/* Title styling */
.closed-title {
  font-size: 30px;
  font-weight: bold;
  color: #c52727;
  margin: 0;
}

/* Styling for the message */
.closed-message {
  font-size: 1.2rem;
  color: #52a124;
  font-style: italic;
  margin-top: 20px;
  max-width: 600px; /* Optional to control the text width */
}


