.cart{
    width: 100%;
    height: 100%;
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    color: #000000;
    justify-content:top center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.Emty{
    font-size: 40px;
    color: chocolate;
}
img{
    width: 300px;
    height: 300px;
}
.ti{
    font-size: 35px;
    color: chocolate;
    text-decoration: underline;
    text-decoration-color: #58b8a4;
    -moz-text-decoration-color: #58b8a4;
}
h3{
    font-size: 25px;
}
.pay{
    width: 300px;
    height: 50px;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-style: italic;
    letter-spacing: 3px;
    color: rgb(18, 16, 24);
    background-color: greenyellow;
    cursor: pointer;
}
.pay:active{
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
}
.liste{
    list-style: none;
    width: auto;
    height: auto;
    min-height: 100px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    align-items: baseline;
    padding: 10px;
    overflow-x: hidden;
}
.pro{
    display: inline;
    border-bottom: 2px solid red;
    padding: 10px;
    width: 100%;
    flex-wrap: wrap;
}
.re{
    font-size: 25px;
    margin-left: 12px;
    background-color: red;
    color: bisque;
}
  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen  and (max-width: 768px) {
    img{
        width: 100px;
        height: 100px;
    }
    .ti{
        font-size: 25px;
        color: chocolate;
        text-decoration: underline;
        text-decoration-color: #58b8a4;
        -moz-text-decoration-color: #58b8a4;
    }
    .Emty{
        font-size: 35px;
        color: chocolate;
    }
  }