.akilia{
    width: 100%;
    height: 100%;
    background-color: #b6e3e8;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    justify-items: center;
    align-items: center;
} 
.titelia{
    color: #000000;
    text-align: center;
    width: 100%;
}
.news-items{
    justify-content: center;
    align-items: center;
}
.mizi{
    margin-left: 42%;
}
.downloads{
    margin-left: 42%;
    width: 400px;
    height: 50px;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    gap: 14px;
    justify-items: top center;
    align-items: center;
    justify-content: top center;
    align-content: center;
}
.downloadsAndroid{
   width: 200px;
   height: 40px;
   color: green;
   background-color: whitesmoke;
   background-image: url("../icons/android.png");
   background-size: 20px;
   background-repeat: no-repeat;
   background-position: left;
}
.downloadIOS{
    width: 200px;
    height: 40px;
    color: rgb(0, 0, 0);
    background-color: rgb(230, 230, 230);
    background-image: url("../icons/apple.png");
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: left;
}
.des{
    color: #000000;
}
.news-item{
    display: flex;
    flex-direction: column;
    justify-content:last baseline
}

  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen  and (max-width: 768px) {
    .container{
        width: 100%;
    }
    .news-items{
        margin-top: -15px;
        width: 100%;
        height: calc(100% + 30%);
    }
    .mizi{
        width: 300px;
        height: 300px;
        margin-left: 10%;
    }
    .des{
        font-size: 14px;
    }
    .downloads{
        margin-left: 10%;
        padding-bottom: 20px;
        z-index: 111111;
    }
      .downloadsAndroid{
        width: 150px;
      }
      .downloadIOS{
        width: 150px;
      }
  }
  
  /* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */