.home{
    width: 100%;
    height: 100%;
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    justify-content: center;
    align-items: center;
    color: #000000;
    border-top: 3px solid 
    rgba(144,30,29,255);
    scroll-behavior: smooth;
    display: inline;
}
.title{
    font-size: 40px;
    text-align: center;
    color: #020202;
    font-style: oblique;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: -4cm;
}
.header{
    height: 50px;
    text-align:  center;
    color:  rgba(144,30,29,255);
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-left: -3cm;
    margin-top: -1cm;
    font-weight: lighter;
    font-style: italic;
}
.loading{
    width: 100%;
    height: 100%;
    font: optional;
    font-size: x-large;
    text-align: center;
}
.infos{
    padding: 12px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-family: sans-serif;
}
.schedule-container {
    width: 100%; /* Can adjust based on your design */
    max-width: 400px; /* Set a max width for responsiveness */
    margin: 0 auto; /* Center the table */
    font-family: Arial, sans-serif; /* Adjust font as needed */
  }
  
  .schedule-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .schedule-item {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid rgba(144,30,29,255); /* Optional: to create table-like rows */
  }
  
  .schedule-day {
    font-weight: bold;
    flex-basis: 40%; /* Day occupies 40% of the row */
  }
  
  .schedule-time {
    flex-basis: 60%; /* Time occupies 60% of the row */
    text-align: right;
    margin-left: 8px;
  }
  .intro{
    font-size: 30px;
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 80%;
    height: 100%;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin-top: -1cm;
    margin-left: 2%;
    z-index: 0.3;
    color: #000000;
  }
  .album{
    width:  800px;
    height: 220px;
    margin-left: 30%;
    background-color: #000000;
    margin-top: 0cm;
    padding: 2px;
  }
  .album-img, .storepic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 1s ease-in-out;
    image-rendering: optimizeQuality;
  }
  
  .fade-in {
    opacity: 1;
    animation: fadeIn 1s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }


/* Medium phones (iPhone 14, 15, modern Androids) */
@media only screen  and (max-width: 768px) {
 .home{
  width: 100%;
height: 100%;
flex-shrink: 0;
margin-top: -25px;
padding: 6px;
border-left: none;
 }
 .title{
  font-size: 20px;
  width: 100%;
  margin-left: -6%;
  text-align: center;
 }
 .header{
  width: 100%;
  height: 20px;
  margin-left: -5%;
  margin-top: -13px;
  padding: 0;
 }
 .infos {
  width: 100%;
  display: flex;
  flex-direction: column-reverse; /* Reverse the order */
  flex-wrap: nowrap; /* Donâ€™t allow wrapping */
  position: relative; /* Relative position */
}

.intro {
  font-size: 16px;
  margin-top: 0;
  z-index: 1;
  padding-bottom: 20px;
}

.schedule-list {
  z-index: 2;
  margin-top: -15px;
  width: 300px;
  height: 200px;
  padding-bottom: 50px;
  /* No extra margin needed */
}
.schedule-list li{
  font-size: 14px;
}

.album {
  margin-left: -15px;
  width: 100%;
  height: 150px;
  z-index: 3;
  margin-top: -10px;
  object-fit: contain;
}
}