.contact-Container {
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    font-family: Arial, sans-serif;
    color: #333;
    margin-top: 5%;
  }
  
  .contact-Container h2 {
    text-align: center;
    color: #007bff;
    font-size: 1.8rem;
    margin-bottom: 20px;
  }
  
  .contact-info {
    margin-top: 10px;
  }
  
  .contact-info h3 {
    text-align: center;
    color: #555;
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  
  .contact-info ul {
    list-style-type: none;
    padding: 0;
  }
  
  .contact-info li {
    margin-bottom: 10px;
    font-size: 1rem;
  }
  
  .contact-info strong {
    color: #333;
  }
  
  .phoni, .mailing {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
  }
  
  .phoni:hover, .mailing:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 500px) {
    .contact-Container {
      max-width: 100%;
      padding: 15px;
    }
  
    .contact-info li {
      font-size: 0.9rem;
    }
  
    .contact-Container h2 {
      font-size: 1.6rem;
    }
  }

