.category-buttons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 20px;
    position: relative;
    padding-bottom: 10px;
    margin-left: 10%;
    box-sizing: border-box;
    z-index: 0;
}

.category-button {
    padding: 10px 20px;
    margin: 0 10px;
    border: none;
    background-color: grey;
    cursor: pointer;
    border-radius: 20px;
    font-size: 16px;
    color: black;
    font-display: initial;
    text-transform: capitalize;
}

.category-button:hover {
    background-color: #ddd;
    color: #58b8a4;
}

.category-button.active {
    background-color: #fbb03b;
    color: white;
    font-weight: bold;
    box-shadow: 3px 3px green;
}

.menu-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding-top: 60px;
    width: 100%;
    height: auto;
}
.ingredient{
    font-size: 12px;
    color: #51db8f;
}
.menu_Container{
    width: 100%;
    height: 100%;
}
.menu-item {
    display: flex;
    flex-direction: column;
    gap: 3px;
    background-color: #fff;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.menu-item img {
    max-width: 100%; /* Ensure responsiveness */
    height: auto; /* Maintain aspect ratio */
    display: block; /* Prevent extra inline space */
    margin: 0 auto 10px; /* Center and add spacing */
    object-fit: cover; /* Ensure the image fits nicely */
}

.menu-item p {
    margin: 5px 0;
}
.products{
    padding: 10px;
    color: black;
    background-image: linear-gradient(103deg, rgba(227,227,232,1) 35%, rgba(230,238,238,1) 50%, #58b8a4 100%);
    width: 100%;
    height: auto;
}
.adder{
    background-color: green;
    font-size: 20px;
    color: bisque;
}
.reducer{
    background-color: rgb(116, 4, 4);
    font-size: 20px;
    color: bisque;
}
  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen  and (max-width: 768px) {
    .menu_Container{
        top: 0;
        width: 100%;
        height: 100%;
        padding: 10px;
        margin-top: -15px;
        overflow-x: hidden;
    }
    .category-buttons {
        flex-wrap: nowrap;  /* Ensure buttons are in one row */
        overflow-x: scroll;  /* Enable horizontal scrolling */
        padding-left: 5px;  /* Small padding for aesthetics */
        scrollbar-width: none; 
        margin-left: -5%;
        /* Hide scrollbar in Firefox */
    }

    .category-buttons::-webkit-scrollbar {
        display: none;  /* Hide scrollbar in Chrome/Safari */
    }
    .menu-items {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));  /* Adjust grid for smaller screens */
        gap: 15px;
        padding: 10px;
    }

    .menu-item {
        width: 70%; 
        /* Ensure menu items scale within grid */
    }
    .menu-item img{
        width: 100%;
        height: 90%;
    }
  }
  
  /* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */
